import './App.scss';
import { useEffect, useState,  } from 'react';
import $ from 'jquery';
import { useNuiEvent } from 'fivem-nui-react-lib';
import useEventListener from '@use-it/event-listener'
import ArrowKeysReact from 'arrow-keys-react';

ArrowKeysReact.config({
  left: () => {
   console.log('left key detected.');
 },
 right: () => {
   console.log('right key detected.');
 },
 up: () => {
   console.log('up key detected.');
 },
 down: () => {
   console.log('down key detected.');
 }
});

var locales;
fetch(`${process.env.PUBLIC_URL}/locales.json`)
.then(response => response.json())
.then(json => {locales = json;})

const ESCAPE_KEYS = ['27', 'Escape'];

function App() {
  const [crit, setCrit] = useState(0);
  const [sel, setSel] = useState(-1);
  const [show, setShow] = useState(false);
  const [selCat, setSelCat] = useState(0);
  const [search, setSearch] = useState("");
  const [cur, setCur] = useState({});
  const [newCur, setNewCur] = useState({});
  const [minMaxVar, setMinMaxVar] = useState({min: 0, max: 1})
  const [curVar, setCurVar] = useState(0);
  const [price, setPrice] = useState(0);
  const _setSelCat =  (cat) => {
    if (selCat + cat >= 0 && selCat + cat <= 6)
    {
      setSelCat(selCat + cat);
    } 
    else if (selCat + cat > 6)
    {
      setSelCat(0);

    } 
    else if (selCat + cat < 0)
    {
      setSelCat(6);
    }
    setSel(cur[selCat]);
    $.post(`https://${GetParentResourceName()}/selCat`, JSON.stringify({selCat: selCat, sel: sel})).done((data) => { // eslint-disable-line
      setMinMaxVar(data)
      setCurVar(data.min);
    })
  }

  function keyUpHandler({ key }) {
    var item_cont = document.getElementsByClassName("items")[0]
    if (ESCAPE_KEYS.includes(String(key))) {
      setShow(false);
      $.post(`https://${GetParentResourceName()}/exit`) // eslint-disable-line
    }
  }

  useEventListener('keyup', keyUpHandler);


  const _setCurVar = (data) => {
    if (curVar + data >= minMaxVar.min && curVar + data <= minMaxVar.max) {
      setCurVar(curVar + data);
    } else if (curVar + data < minMaxVar.min) {
      setCurVar(minMaxVar.max)
    } else if (curVar + data > minMaxVar.max) {
      setCurVar(minMaxVar.min)
    }
    $.post(`https://${GetParentResourceName()}/selVar`, JSON.stringify({selCat: selCat, var: curVar})) // eslint-disable-line
  }

  const [clothes, setClothes] = useState([]);
  
  const _setShow = (data) => {
    setSelCat(0);
    setMinMaxVar(data.minmax)
    setCurVar(data.minmax.min);
    setCur([].concat(data.cur));
    setNewCur([].concat(data.cur));
    setSel(data.cur[0]);
    setClothes(data.clothes);
    setShow(data.show);
    setPrice(0);
  }

  const _setSel = (num) => {
    setSel(num);
    newCur[selCat] = num;
    var cc = newCur
    cc[selCat] = num;
    setNewCur(cc);
    var newPrice = 0;
    for (var i=0; i < 6; i++) {
      if (cur[i] != newCur[i]) {
        for (var j=0; j < clothes[i].length; j++) {
          if (clothes[i][j].varId == num) {
            newPrice += clothes[i][j].price
          }
        }
      }
    }
    setPrice(newPrice);
    $.post(`https://${GetParentResourceName()}/setSel`, JSON.stringify({sel: num, cur: selCat})).done((data) => { // eslint-disable-line
      setMinMaxVar(data)
      setCurVar(data.min);
    }) 
  }

  useNuiEvent("app", "setShow", _setShow)

  const pay = (state) => {
    $.post(`https://${GetParentResourceName()}/buy`, JSON.stringify({price: price, state: state})) // eslint-disable-line
    setShow(false);
  }

  const getLabel = () => {
    return {__html: locales.clothingShop}
  }

  return (
    <>
      {show ?
      <div className="App">
      <div className="label-store" dangerouslySetInnerHTML={getLabel()}/>
      <div className="slider-right"/>
      <div className="slider"/>
        <div className="left">
          <div className="search-container">
            <div className="search">
              <input placeholder={locales.searchByName} onInput={(event) => {setSearch(event.target.value)}}></input>
            </div>
            <div className="criterias">
              <div className={`criteria ${crit == 0 ? "active" : ""}`} onClick={() => {setCrit(0)}}>
                <div className="icon up"/>&nbsp;&nbsp;{locales.expensive}
              </div>
              <div className={`criteria ${crit == 1 ? "active" : ""}`} onClick={() => {setCrit(1)}}>
                <div className="icon down"/>&nbsp;&nbsp;{locales.cheap}
              </div>
            </div>
          </div>
          <div className="items">
            {clothes && clothes.length > 0 ? 
              [].concat(clothes[selCat]).sort((a, b) => crit == 0 ? (a.price < b.price ? 1 : -1) : (a.price > b.price ? 1 : -1)).map((value, index) => 
              <div>
                {value.label.toUpperCase().includes(search.toUpperCase()) &&
                <div className={`item ${sel == value.varId ? "active" : ""}`} onClick={() => {_setSel(value.varId)}}>
                  {value.label}
                  <div className="price">{value.price}$</div>
                </div>   
                }
              </div>
              )
            : <center className="noClothingAvailable">{locales.noClothingAvailable}</center>}
          </div>
          <div className="parts">
            <div className={`part ${selCat == 0 ? "active" : ""}`}>
              <div className="icon-cont">
                <img src={process.env.PUBLIC_URL + "/parts/hats.svg"} style={{fill: "black"}} className="icon"/>
              </div>
              <div className="label">{locales.helmet}</div>
            </div>
            <div className="spacer"/>
            <div className={`part ${selCat == 1 ? "active" : ""}`}>
              <div className="icon-cont">
                <img src={process.env.PUBLIC_URL + "/parts/mask.svg"} style={{fill: "black"}} className="icon"/>
              </div>
              <div className="label">{locales.mask}</div>
            </div>
            <div className="spacer"/>
            <div className={`part ${selCat == 2 ? "active" : ""}`}>
              <div className="icon-cont">
                <img src={process.env.PUBLIC_URL + "/parts/tshirt.svg"} style={{fill: "black"}} className="icon"/>
              </div>
              <div className="label">{locales.tshirt}</div>
            </div>
            <div className="spacer"/>
            <div className={`part ${selCat == 3 ? "active" : ""}`}>
              <div className="icon-cont">
                <img src={process.env.PUBLIC_URL + "/parts/torso.svg"} style={{fill: "black"}} className="icon"/>
              </div>
              <div className="label">{locales.shirt}</div>
            </div>
            <div className="spacer"/>
            <div className={`part ${selCat == 4 ? "active" : ""}`}>
              <div className="icon-cont">
                <img src={process.env.PUBLIC_URL + "/parts/arms.svg"} style={{fill: "black"}} className="icon"/>
              </div>
              <div className="label">{locales.arms}</div>
            </div>
            <div className="spacer"/>
            <div className={`part ${selCat == 5 ? "active" : ""}`}>
              <div className="icon-cont">
                <img src={process.env.PUBLIC_URL + "/parts/pants.svg"} style={{fill: "black"}} className="icon"/>
              </div>
              <div className="label">{locales.pants}</div>
            </div>
            <div className="spacer"/>
            <div className={`part ${selCat == 6 ? "active" : ""}`}>
              <div className="icon-cont">
                <img src={process.env.PUBLIC_URL + "/parts/shoes.svg"} style={{fill: "black"}} className="icon"/>
              </div>
              <div className="label">{locales.shoes}</div>
            </div>
          </div>
        </div>
          <div className="nav-icon" id="up" onClick={() => {_setSelCat(-1)}}></div>
          <div className="nav-icon" id="down" onClick={() => {_setSelCat(1)}}></div>
        <div className="right">
          <div className="variation-label">{locales.clothingVariations}</div>
          <div className="variations">
            <div className="label">{locales.clothingVariation + curVar}</div>
            <div className="arrow right" onClick={() => {_setCurVar(1)}}/>
            <div className="arrow left" onClick={() => {_setCurVar(-1)}}/>
          </div>
          <div className="total-price">{locales.checkout}</div>
          <div className="price-overview">
            <div className="store-time">{locales.shopName}<span className="right">{(new Date().getHours()) + ":" + (new Date().getMinutes())}</span></div>
            <div className="all-price">{locales.totalPrice}</div>
            <div className="price">{locales.currency + "" + price}</div>
            <div className="btns">
              <button className="active" onClick={() => {pay(false)}}>{locales.payWithCash}</button>
              <button onClick={() => {pay(true)}}>{locales.payWithCard}</button>
            </div>
          </div>
        </div>
        <div className="camera-settings">
          <div className="label">
            {locales.changeCam}
          </div>
          <div className="cams">
            <div className="cam">
              <span className="label">{locales.height}</span>
              <input type="range" min={-20} max={50} defaultValue={15} onInput={(event) => {$.post(`https://${GetParentResourceName()}/height`, JSON.stringify({h: event.target.value})) /* eslint-disable-line */}}></input>
            </div>
            <div className="cam">
              <span className="label">{locales.rotation}</span>
              <input type="range" min={-180} max={180} defaultValue={0} onInput={(event) => {$.post(`https://${GetParentResourceName()}/rotation`, JSON.stringify({d: event.target.value})) /* eslint-disable-line */}}></input>
            </div>
            <div className="cam">
              <span className="label">{locales.distance}</span>
              <input type="range" min={20} max={120} defaultValue={75} onInput={(event) => {$.post(`https://${GetParentResourceName()}/distance`, JSON.stringify({e: event.target.value})) /* eslint-disable-line */}}></input>
            </div>
          </div>
        </div>
      </div> : <div></div>}
    </>
  );
}

export default App;